import * as React from 'react';
import { useRequest } from '@miq/hookjs';
import Form, { useForm } from '@miq/formjs';
import { Response } from '@miq/utiljs';
import Staff from '..';
import { Index } from './models';

//#region IndexModel

//#endregion IndexModel

//#region IndexUpdateView

export default function IndexUpdateView() {
  const form = useForm({ title: '' });
  const { res, setRes, setError } = useRequest(() => Index({ slug: 'current' }).retrieve(), {
    refreshDeps: [],
    onSuccess: ({ data }: any) => form.setValues({ title: data.title || '' }),
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  const instance = Index(r.data);

  const handleSubmit = (e: any) => {
    e.preventDefault();

    return instance
      .update(form.values)
      .then(({ data }: any) => setRes(data))
      .catch(({ response }: any) => setError(response));
  };

  return (
    <Staff.View title="Site HomePage Settings" back="../">
      <Form context={form} onSubmit={handleSubmit}>
        <Staff.Section title="Index Page Details">
          <Form.Field label="Meta title" error={form.errors.title}>
            <Form.Text required name="title" maxLength={99} />
          </Form.Field>

          <div className="">
            <Form.Submit value="Save" className="btn-primary-2" />
          </div>
        </Staff.Section>
      </Form>
    </Staff.View>
  );
}

//#endregion IndexUpdateView
