import { ImgProps } from '@miq/componentjs';
import { StaffModel, StaffService } from '../service';

export type TSite = {
  navbar?: { label: string; path: string }[];
  /** Site setting slug */
  settings?: string;
  name?: string;
  domain?: string;
};

export type TSetting = Record<string, any> & {
  slug?: string;
  is_live?: string;
  site?: TSite;

  logo?: string;
  logo_data?: ImgProps;

  contact_number?: string;
  contact_number_title?: string;
  contact_number_display?: string;
  contact_email?: string;
  whatsapp_link?: string;
  whatsapp_link_title?: string;

  ga_tracking?: string;
  fb_pixel?: string;
  fb_app_id?: string;
  fb_app_secret?: string;

  ct_title?: string;
  ct_text?: string;
  ct_html?: string;
  ct_image?: string;
  ct_image_data?: ImgProps;
};

export class SettingService extends StaffService<TSetting> {
  updatePages(values: any) {
    return this.patch(`${this._path}pages/`, values);
  }

  updateSite(values: any) {
    return this.patch(`${this._path}site/`, values);
  }
}

export function Setting(data: TSetting = {}) {
  return StaffModel<TSetting, SettingService>(data, 'settings/', SettingService);
}

export type TSettingInstance = ReturnType<typeof Setting>;

export type TIndex = {};

export function Index(data: TIndex = {}) {
  return StaffModel<TIndex>(data, 'index/');
}

export type TIndexInstance = ReturnType<typeof Index>;

